.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.button-top {
  z-index: 100;
  height: 40px;
  width: 40px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: scale(1);
  box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029;
}

.button-top:hover, .button-top:focus {
  transform: scale(1.5);
}

.button-top-icon {
  fill: #d41443;
  transform: rotate(-90deg);
}

.mobile-menu {
  width: 250px;
  height: 100vh;
  z-index: 99;
  background-color: #e17992;
  flex-direction: column;
  padding-top: 20px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: calc(50% - 135px);
  }
}

