  height: 44px;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  justify-content: space-evenly;
  align-self: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.3;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
