.about {
    padding-top: 60px;
    padding-bottom: 40px;

    background-color: rgba(255, 245, 246, 1);

    @include mq(tablet){
        padding-bottom: 60px;
    }

    &__wrapper {
        @include mq(mobile-only) {
            background: url(../images/about/milk-bg-left.png) top 596px left 0 no-repeat, url(../images/about/milk-bg-right.png) top 600px right 0 no-repeat;
            background-color: rgba(255, 245, 246, 1);

            @include mq(retina) {
                background: url(../images/about/milk-bg-left@2x.png) top 596px left 0 no-repeat, url(../images/about/milk-bg-right@2x.png) top 600px right 0 no-repeat;
                background-color: rgba(255, 245, 246, 1);
            }
        }

        @include mq(desktop) {
            background: url(../images/about/milk-bg-desk-left.png) top 260px left 0 no-repeat, url(../images/about/milk-bg-desk-right.png) top 280px right 0 no-repeat;
            background-color: rgba(255, 245, 246, 1);

            @include mq(retina) {
                background: url(../images/about/milk-bg-desk-left@2x.png) top 260px left 0 no-repeat, url(../images/about/milk-bg-desk-right@2x.png) top 280px right 0 no-repeat;
                background-color: rgba(255, 245, 246, 1);
                background-size: 20%;
            }
        }
    }

    &__content {
        @include mq(tablet-only) {
            display: flex;
            align-items: start;
            justify-content: center;
        }
        @include mq(desktop){
            display: flex;
            align-items: start;
            justify-content: center;
        }
    }

    &__description {
        max-width: 220px;
        margin-right: auto;
        margin-bottom: 20px;

        text-align: start;
        font-size: 12px;
        line-height: 1.62;
        letter-spacing: 0em;

        @include mq(tablet-only) {
            max-width: 334px;
            font-size: 14px;
            line-height: 1.76;
        }

        @include mq(desktop) {
            max-width: 463px;
            margin-bottom: 32px;
            font-size: 16px;
            line-height: 1.76;
            letter-spacing: 0.04em;
        }
    }

    &__description--grey {
        max-width: 220px;

        margin-right: auto;
        margin-bottom: 20px;

        text-align: start;
        font-size: 12px;
        line-height: 1.62;
        letter-spacing: 0.02em;

        color: rgba(144, 126, 130, 1);

        @include mq(tablet-only) {
            max-width: 334px;
            margin-bottom: 40px;

            font-size: 14px;
            line-height: 1.76;
            letter-spacing: 0.04em;
        }

        @include mq(desktop) {
            max-width: 463px;
            margin-bottom: 40px;
            
            font-size: 14px;
            line-height: 1.94;
            letter-spacing: 0.04em;
        }
    }

    &__text-wrapper {
        @include mq(mobile-only) {
            max-width: 280px;
            margin-left: auto;
            margin-right: auto;
        }

        @include mq(tablet-only) {
            max-width: 334px;
        }

        @include mq(desktop) {
            max-width: 463px;
            margin-left: 99px;
            margin-top: 90px;
        }
    }
}

.bg-cow {
    @include mq(mobile-only) {
        max-width: 280px;
        height: 279px;
        margin-bottom: 40px;
        margin-left: auto;
        margin-right: auto;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url(../images/about/cow_280.png);

        @include mq(retina){
            background-image: url(../images/about/cow_560.png);
        }
    }

    @include mq(tablet-only) {
        width: 336px;
        height: 335px;
        margin-right: 32px;
        background-size: contain;

        background-image: url(../images/about/cow_336.png);
        background-position: center;

        @include mq(retina){
            background-image: url(../images/about/cow_672.png);
        }
    }

    @include mq(desktop) {
        width: 501px;
        height: 501px;

        background-image: url(../images/about/cow_501.png);
        background-position: center;
        background-size: contain;

        @include mq(retina){
            background-image: url(../images/about/cow_1002.png);
        }
    }
}

.about-button {
    display: flex;
    align-items: center;

    padding-left: 37px;
    padding-right: 38px;
    padding-top: 14px;
    padding-bottom: 13px;

    max-width: 176px;
    width: 100%;

    font-weight: 700;
    line-height: 1.29;

    color: rgba(212, 20, 67, 1);
    background-color: rgba(255, 255, 255, 1);
    border-radius: 22px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover, &:focus {
        transform: scale(1.1);
        background-color: rgba(212, 20, 67, 1);
        color: rgba(255, 255, 255, 1);
    }

    &__icon {
        color: currentColor;
        margin-left: auto;
        fill: rgba(212, 20, 67, 1);
        min-width: 9px;
        min-height: 13px;
        transform: rotate(90deg);
    }

    &:hover .about-button__icon, &:focus .about-button__icon {
        fill: rgba(255, 255, 255, 1);
    }

    &--hidden {
        display: none;
    }
}

.about-button--hide {
    display: none;
    align-items: center;

    padding-left: 55px;
    padding-right: 38px;
    padding-top: 14px;
    padding-bottom: 13px;

    max-width: 176px;
    width: 100%;

    font-weight: 700;
    line-height: 1.29;

    color: rgba(212, 20, 67, 1);
    background-color: rgba(255, 255, 255, 1);
    border-radius: 22px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
        transform: scale(1);
        transition: 
            transform 250ms cubic-bezier(0.4, 0, 0.2, 1), 
            background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            color 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover, &:focus {
            transform: scale(1.1);
            background-color: rgba(212, 20, 67, 1);
            color: rgba(255, 255, 255, 1);
        }

        &:hover .about-button--hide__icon, &:focus .about-button--hide__icon {
            fill: rgba(255, 255, 255, 1);
        }

    &__icon{
        color: currentColor;
        margin-left: 35px;
        fill: rgba(212, 20, 67, 1);
        min-width: 9px;
        min-height: 13px;
        transform: rotate(270deg);
    }

    &--appear {
        display: flex;
    }
}


// шрифты для тайтл

.about .title {
    margin-bottom: 32px;
    text-align: center;

    @include mq(tablet-only){
        margin-bottom: 40px;
        font-size: 48px;
    }

    @include mq(desktop) {
        margin-bottom: 60px;
        font-size: 58px;
        line-height: 1.138;
    }
}

.about .subtitle {
        margin-bottom: 15px;
        text-align: center;
        
        @include mq(tablet-only){
            margin-bottom: 16px;
            font-size: 26px;
        }

        @include mq(desktop) {
            margin-bottom: 16px;
            font-size: 30px;
            line-height: 1.133;
        }
}